import React from 'react';
import { Collapse, Select,Switch } from 'antd';
import { changeToAnimation } from '../../../action/storyAction';
import { animationFlag } from '../../../selector/story';
const { Panel } = Collapse;
const {Option} = Select;

var settledTimeOut = 0;
var loopOrNot = false;
//var animationType = this.props.animationType;
var animationType = "顺序播放"
var animateOrNot = false;

export default class AnimationSettings extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            
        }
    }
    // 静态图表
    staticChartsShow = () => {
        //return new Promise(async (resolve, reject) => {
            
            for(let i =0;i<=settledTimeOut;i++){
                clearTimeout(i);
            }
            settledTimeOut = 0;

            let tempFacts = [];

            for (let factDict of this.props.facts) {
                
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    factDict['chart'],//chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = factDict.generatedScript; //edit script
                fact.duration = factDict.duration;//save duration
                tempFacts.push(fact);
            }

            this.props.generateStory(tempFacts.slice(), [
                 "none",
                "similarity",
                "similarity",
                 "similarity",
                "similarity",
                 "similarity"
             ], 1);

            this.setState({
                operateState: OperationType.GENERATED
            })
            this.props.isUpdateEditing(false)
            
            console.log("静态图表")
            // 静态图表
        //})
    }


    // 顺序播放
    playInSequence = () => {
        
        if(animateOrNot && animationType == "顺序播放")
        {
            let seuqenceLentgh = this.props.facts.length;

            for(let i =0;i<=settledTimeOut;i++){
                clearTimeout(i);
            }
            settledTimeOut = 0;

            let tempFacts = [];
        
            let updatedFact = 0;
            for (let factDict of this.props.facts) {
                

                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    factDict['chart'],//chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = factDict.generatedScript; //edit script
                fact.duration = factDict.duration;//save duration
                tempFacts.push(fact);
                // 依次改变每个fact的state
                if(updatedFact==0)//update第一个fact
                {
                    if(this.props.facts.length<seuqenceLentgh) //如果删除fact，导致fact长度变小
                    {
                        for(let i =0;i<=settledTimeOut;i++){
                            clearTimeout(i);
                        }
                        settledTimeOut = 0;
                        this.playInSequence();
                        return;
                    }
                    else
                    {
                        if(this.props.facts.length>seuqenceLentgh) //如果添加了fact当前的循环要停止
                        {
                            for(let i =0;i<=settledTimeOut;i++){
                                clearTimeout(i);
                            }
                            return;
                        }
                        else
                        {
                            this.props.updateFact(updatedFact, fact);
                            console.log("first fact"+updatedFact+"-------------------------------");
                            if(updatedFact==this.props.facts.length-1)//单个facts时循环
                                {
                                    if(animateOrNot &&loopOrNot && animationType == "顺序播放")
                                    {
                                        settledTimeOut = setTimeout(()=>{// loop
                                            
                                            this.playInSequence();
                                        },(tempFacts[updatedFact].duration+1000));
                                    }
                                }
                        }
                    }
                }
                else{//update其余的fact
                    
                    let factNow = _.cloneDeep(fact);
                    let updatedFactNow = updatedFact;
                    let waitingTime = function(){//算出每个fact需要等待的时长
                        let totalWaitingTime = 0;
                        for(let j=0;j<updatedFactNow;j++){
                            totalWaitingTime += tempFacts[j].duration;//应该为之前所有fact的duration之和
                        }
                        return totalWaitingTime;
                    }
                    settledTimeOut = setTimeout(()=>{//给fact的update延迟时间
                        if(this.props.facts.length<seuqenceLentgh) //如果删除fact，导致fact长度变小
                        {
                            for(let i =0;i<=settledTimeOut;i++){
                                clearTimeout(i);
                            }
                            settledTimeOut = 0;
                            this.playInSequence();
                            return;
                        }
                        else
                        {
                            if(this.props.facts.length>seuqenceLentgh) //如果添加了fact当前的循环要停止
                            {
                                for(let i =0;i<=settledTimeOut;i++){
                                    clearTimeout(i);
                                }
                                return;
                            }
                            else
                            {
                                this.props.updateFact(updatedFactNow, factNow);// 如果正常，就继续播放
                                if(updatedFactNow==this.props.facts.length-1)
                                {
                                    if(animateOrNot &&loopOrNot && animationType == "顺序播放")
                                    {
                                        settledTimeOut = setTimeout(()=>{// loop
                                            
                                            this.playInSequence();
                                        },(tempFacts[updatedFactNow].duration)+1000);
                                    }
                                }
                            }
                        }
                    },waitingTime()+1000);
                    console.log("fact: "+updatedFact+"-------------------------------");
                    console.log("facts length: "+ this.props.facts.length+"-------------------------------")
                }
                
                updatedFact++;
            }
            console.log("顺序播放")
            // 顺序播放
        }
    }


    // 同步播放
    playTogether = () => {
        
        if(animateOrNot && animationType == "同步播放")
        {
            let togetherlength = this.props.facts.length;
            

            for(let i =0;i<=settledTimeOut;i++){
                clearTimeout(i);
            }
            settledTimeOut = 0;

            let tempFacts = [];
            let longestWaitingTime = 0;

            for (let factDict of this.props.facts) {
                
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    factDict['chart'],//chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = factDict.generatedScript; //edit script
                fact.duration = factDict.duration;//save duration
                tempFacts.push(fact);

                if(longestWaitingTime<fact.duration)// 寻找最长的duration
                {
                    longestWaitingTime = fact.duration
                }
            }
            
            this.props.generateStory(tempFacts.slice(), [
                 "none",
                "similarity",
                "similarity",
                 "similarity",
                "similarity",
                 "similarity"
             ], 1);

             if(animateOrNot && loopOrNot && animationType == "同步播放")
             {
                 settledTimeOut = setTimeout(()=>{// loop
                    if(this.props.facts.length>togetherlength) //如果添加了fact停止循环
                    {
                        for(let i =0;i<=settledTimeOut;i++){
                            clearTimeout(i);
                        }
                        return;
                                    
                    }
                    else
                    {
                        if(this.props.facts.length<togetherlength)
                        {
                            for(let i =0;i<=settledTimeOut;i++){
                                clearTimeout(i);
                            }
                            this.playTogether();
                            return;
                        }
                        else
                        {
                            if(loopOrNot)
                            {this.playTogether();}
                        }
                    }
                },(longestWaitingTime+1000));
             }

            
            console.log("同步播放")
            // 同步播放
        }
    }


    
    onChange = (checked) => {

        if(checked)
        {
            this.props.changeToAnimation(true);
            console.log("输出animation flag = "+this.props.animationFlag);
            animateOrNot = true;
            console.log("输出animateOrNot = "+ animateOrNot);

            if(animationType=="顺序播放")
                {
                    this.playInSequence();
                }
            else if (animationType=="同步播放")
                {
                    this.playTogether();
                }
                else
                {
                    this.playInSequence();
                }
        }
        else
        {
            this.props.changeToAnimation(false);
            console.log("输出animation flag = "+this.props.animationFlag);
            animateOrNot = false;
            console.log("输出animateOrNot = "+ animateOrNot);
            
            this.staticChartsShow();
        }

    }


    onLoop = (checked) => {
        if(checked)
        {
            loopOrNot = true;
            console.log("loop or not: "+loopOrNot+"----------------------------------")
        }
        else
        {
            loopOrNot = false;
            console.log("loop or not: "+loopOrNot+"----------------------------------")
        }

    }

    onSelect = (value) => {
        if(value == "In Sequence" || value == "顺序播放")
        {
            animationType = "顺序播放";
            this.playInSequence();
            
        }
        else if(value == "Together" || value == "同步播放")
        {
            animationType = "同步播放";
            this.playTogether();
            
        }
            else 
            {
                animationType = "顺序播放";
                this.playInSequence();
            }
    }


    render(){
        const { intl, initDone} = this.props;

        
        return(
             <Collapse defaultActiveKey={["animationFilter-data-1", "animationFilter-data-2", "animationFilter-data-3"]}>
                <Panel header={initDone && intl.get("Animation Settings")} key="animationFilter-data-1" >
                {/* animation or not */}
                
                <div>
                    <div style={{display: "inline-block"}}>
                        <p>{initDone && intl.get("Animated Charts")}</p>
                    </div>
                    <div style={{float:"right",display: "inline-block"}}>
                        <Switch defaultChecked={this.props.animationFlag} onChange={this.onChange}></Switch>
                    </div>
                </div>

                {/* loop or not */}
                <div>
                    <div style={{display: "inline-block"}}>
                        <p>{initDone && intl.get("Loop")}</p>
                    </div>
                    <div style={{float:"right",display: "inline-block"}}>
                        <Switch defaultChecked={loopOrNot} onChange={this.onLoop}></Switch>
                    </div>
                </div>

                {/* play in which way */}
                <div>
                    <div style={{display: "inline-block",height:"30px"}}>
                        <p style={{lineHeight:'2.2'}}>{initDone && intl.get("Play Type")}</p>
                    </div>
                    <div style={{float:"right",display: "inline-block"}}>
                        <Select defaultValue={animationType=="顺序播放" ? initDone && intl.get("Play in Sequence"):initDone && intl.get("Play Together")}  style = {{width:120}} onChange = {this.onSelect}>
                            <Option value = {initDone && intl.get("Play in Sequence")}>{initDone && intl.get("Play in Sequence")}</Option>
                            <Option value = {initDone && intl.get("Play Together")}>{initDone && intl.get("Play Together")}</Option>
                        </Select>
                    </div>
                </div>

                </Panel>
            </Collapse>
        )
    }
}